import {NOTE_TYPE} from "../data/data";
import NotesLink from "./NotesLink";
import NotesText from "./NotesText";
import NoteBuilder from "./NoteBuilder";
import {useState} from "react";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faPlus} from "@fortawesome/free-solid-svg-icons";

const ProjectContant = props => {
    const[addNote,setAddNotes]=useState(
        false
    )
    const[noteInfo,setNoteSubmission]=useState({
            type:"",
            text:"",
            title:"",
            url:"",
            // project:"",
        }
    )

    const noteSubmitted = (info, noteName) => {
        /** The object passed to setSubmission involves two JS shortcuts:
         * ...info means merge the info object with the following values
         * formName in this context is a shortcut for formName: formName
         * The longhand equivalent would be:
         * {
         *   phone: info.phone,
         *   phoneType: info.phoneType,
         *   email: info.email,
         *   formName: formName
         * }
         */
        setNoteSubmission({...info, noteName})
    }




    return(
        <div>
            <div>
                {
                    Object.entries(props.subject).map(([key, subSubject], i) => (
                            (subSubject.type === NOTE_TYPE.LINK) || (subSubject.type === NOTE_TYPE.text)
                                ?
                                <NotesLink
                                    url={subSubject.url}
                                    text={subSubject.text}
                                /> : <NotesText
                                    title={subSubject.title}
                                    text={subSubject.text}
                                />
                        )
                    )
                }
            </div>
            <div className="row">
                <div className="col">
                    {
                        (noteInfo.type!=="")&&((noteInfo.type === NOTE_TYPE.LINK) || (noteInfo.type === NOTE_TYPE.text)
                            ?
                            <NotesLink
                                url={noteInfo.url}
                                text={noteInfo.text}
                            /> : <NotesText
                                title={noteInfo.title}
                                text={noteInfo.text}
                            />)
                    }
                </div>
            </div>
            <div className="row newNoteButton">
                    <div className="form-check">
                        <input className="btn-check" type="checkbox" id={"addNotes"+props.projectName}
                               checked={addNote}  onChange={() => setAddNotes(!addNote)}/>
                        <label className="btn btn-primary buttonCustom"  htmlFor={"addNotes"+props.projectName}> <FontAwesomeIcon icon={faPlus} /> add notes</label>
                    </div>
            </div>
            <div className="row">
                <div className="col">{

                    addNote?
                        <NoteBuilder submit={noteSubmitted} />:""
                }

                </div>
            </div>
        </div>

    )
}

export default ProjectContant;
